import React from "react";
import { PostTrimContent } from '../../../../utils';
import { isTagShop, isTagbox } from "../../../../actions/api";
import Media from './Media';
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import Network from "../../Elements/Network/Network";
import ShopButton from "../../Elements/ShopButton/ShopButton";
import AuthorDetails from "./AuthorDetails";
import PostContent from '../../Elements/PostContent/PostContent';
import InViewWrapper from "../../Elements/InViewWrapper";

const trim_content_length = 200;

const Card = React.memo(({ Post, adjustWidth, CardStyle, clickToShowPopUp, index, ThemeInfo, ThemeStyle, postSizeInPx, onClickPopUpSlider }) => {
  const { networkId, share } = Post;
  const { style, content } = CardStyle;

  // Memoize expensive calculations
  const mediaType = Post.type !== 1 ? true : false
  const postTextContent = CardStyle.content.status ? PostTrimContent(Post.content.text, trim_content_length) : Post.content.text
  const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false
  const overlay = `tb_sp_${isTagbox ? 'color_overlay' : `post_overlay-${CardStyle.style.mode === 0 && isTagShop ? 'light' : 'dark'}`}`;
  const overlayStyle = { backgroundColor: isTagbox ? style.background : '' }

  return <InViewWrapper>
    {({ ref, inView }) => (
      <div id={`tb_sp_id-${Post.id}`}
        className={`tb_sp_post_wrapper${responsiveCardSize2x ? ' tb_sp_post_wrapper2x' : ''}`} style={{ width: `${adjustWidth}%`, padding: style.padding / 2, opacity: inView ? 1 : 0, transition: inView ? 'opacity .5s ease-in-out' : '' }} tb-network={Post.network.id} tb-product={CardStyle.shopText != '' && Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} role="article" aria-label={`Post ${parseInt(index) + 1},${postTextContent}`} post-id={Post.referenceId ? Post.referenceId : Post.id} tabIndex="0" ref={ref} feed-id={Post.feedId}>
        <div className={`tb_sp_post_in${isTagShop ? ' tb_sp_post_ani' : ''}`} onClick={clickToShowPopUp(index, Post)} role="button" aria-label="Popup" tabIndex={0}>
          <div className="tb_sp_post_media_wrapp" role="button" tabIndex={0} aria-label="Pop Up Button">
            {share.status && <ShareOnHover Post={Post} ThemeInfo={ThemeInfo} shareClass={"tb_sp_share_container"} />}
            {mediaType && <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} inView={inView} />}
            {!Post.isHotspot && (
              <>
                <div className="tb_sp_post_details">
                  {isTagShop && (
                    <div className="tb_sp_social_">
                      <Network networkClass={'tb_sp_social__ico'} networkId={networkId} iconStyle={CardStyle.icon} />
                    </div>
                  )}
                  {CardStyle.shopText != '' && Post.isShopIcon && <ShopButton CardStyle={CardStyle} mode={1} btnClass={'tb_sp_btn_wrap'} />}
                  {!isTagShop && (
                    <>
                      {ThemeInfo.id !== 65 && (
                        <div className="tb_sp_contant_wrapper">
                          {(!CardStyle.content.status || Post.type === 1) && (
                            <PostContent contentClass={'tb_sp_content'} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={content.title} />
                          )}
                        </div>
                      )}
                      <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} />
                    </>
                  )}
                </div>
                <div className={`tb_sp_post_overlay ${overlay}`} style={overlayStyle}><div></div></div>
              </>
            )}
          </div>
        </div>
      </div>
    )}
  </InViewWrapper>
});

export default Card;