import React, { Suspense, lazy } from "react";
const Author = lazy(() => import("../../Elements/Author/Author"));
const Time = lazy(() => import("../../Elements/Time/Time"));
const Network = lazy(() => import("../../Elements/Network/Network"));
const Seprator = lazy(() => import("../../Elements/Seprator/Seprator"));

const AuthorDetails = ({ Post, CardStyle }) => {
  const { author, networkId } = Post
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (CardStyle.author.status && !author.isInstaUser) ? true : false;
  const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !author.isInstaUser) ? true : false;
  return <div className="tb_sp_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className="tb_sp_author">
      {postAuthor ? <Suspense fallback={<></>}><Author author={author} network={Post.network} authorClass={'tb_sp_author_profile'} /></Suspense> : ''}
      <div className="tb_sp_author_info">
        {postAuthor ? <div className="tb_sp_authorname">{author.name}</div> : ''}
        <div className="tb_sp_post_info">
          {postAuthor ? <div className="tb_sp_username">{username}</div> : ''}
          {authorSeprator ? <Seprator sepClass={`tb_sp_seprator`} /> : ''}
          {CardStyle.timeStatus ? <Suspense fallback={<></>}><Time postTime={Post.createdAt} timeClass={`tb_sp_time tb-cTBfont-${CardStyle.author.variant}`}  /> </Suspense> : ''}
        </div>
      </div>
    </div>
      <div className="tb_sp_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_sp_social__ico'} networkId={networkId} iconStyle={CardStyle.icon} /> </Suspense>
      </div> 
  </div>
}
export default AuthorDetails