import React from "react";
import Image from '../../Elements/Media/Image';
import Video from '../../Elements/Media/Video';
import { isTagShop } from "../../../../actions/api";
import MediaIcon from '../../Elements/MediaIcon/MediaIcon';
const Media = ({ Post, ThemeStyle, index, inView }) => {
  const mediaIcon = Post.hotspot ? false : true
  const isVideo = ([3,5].includes(Post.type)) && (ThemeStyle.autoPlay == 1) ? true : false
  return <div className="tb_sp_media_wrap">
      <MediaIcon Post={Post} IconClass={`tb_sp_media_icon`} show={mediaIcon}  hideVideo={ThemeStyle.autoPlay} />
      {isVideo ? 
      <Video VideoClass="tb_sp_video" Post={Post} size={100} isCover={true} controls={false} autoPlay={true} muted={true} observer={true} index={index} inView={inView} /> 
      :
      <Image ImageClass="tb_sp_image" Post={Post} size={100} hotspot={true} blurBG={isTagShop} observer={true} index={index} inView={inView} /> }
  </div>
}
export default Media